import { useState } from 'react';
import { useSpring, animated, config } from 'react-spring';
import delay from "delay";
import './App.css';
import './Info.css';


function hslToRgb(h, s, l) {
  const H = h % 360;
  const S = s / 100;
  const L = l / 100;

  const C = (1 - Math.abs(2 * L - 1)) * S;
  const X = C * (1 - Math.abs((H / 60) % 2 - 1));
  const m = L - C / 2;
  let r_, g_, b_;
  if (0 <= H && H < 60) [r_, g_, b_] = [C, X, 0];
  if (60 <= H && H < 120) [r_, g_, b_] = [X, C, 0];
  if (120 <= H && H < 180) [r_, g_, b_] = [0, C, X];
  if (180 <= H && H < 240) [r_, g_, b_] = [0, X, C];
  if (240 <= H && H < 300) [r_, g_, b_] = [X, 0, C];
  if (300 <= H && H < 360) [r_, g_, b_] = [C, 0, X];

  return {
    R: Math.floor((r_ + m) * 255 + 0.5),
    G: Math.floor((g_ + m) * 255 + 0.5),
    B: Math.floor((b_ + m) * 255 + 0.5)
  };
}

function randomConfig() {
  const configs = [config.default, config.gentle, config.molasses, config.slow, config.stiff, config.wobbly];
  const idx = Math.floor(Math.random() * configs.length);
  return configs[idx];
}

function App() {
  const [idx, setIdx] = useState(0);
  const hue = Math.floor(Math.random() * 360);
  const sat = Math.floor(Math.random() * 100);
  const lig = Math.floor(Math.random() * 100);
  const hsl = `hsl(${hue}, ${sat}%, ${lig}%)`;
  const props = useSpring({
    to: { background: hsl },
    config: randomConfig()
  });

  const rgb = hslToRgb(hue, sat, lig);
  const infoProps = useSpring({
    from: { opacity: 0 },
    to: async (next, cancel) => {
      await delay(300);
      await next({ opacity: 1 });
      await delay(5000);
      await next({ opacity: 0 });
    },
    config: config.gentle,
  });

  return (
    <animated.div className="App" style={props} onClick={(e) => setIdx(i => i + 1)}>
      <animated.div className="Info" style={infoProps}>
        <span style={{ color: lig < 60 ? 'white' : 'black' }}>{hsl}</span>
        <span style={{ color: lig < 60 ? 'white' : 'black' }}>{`rgb(${rgb.R}, ${rgb.G}, ${rgb.B})`}</span>
      </animated.div>
    </animated.div >
  );
}

export default App;
